import React from 'react'
import ReactDOM from 'react-dom'

import '../styles/demo.css'
class DemoComponent extends React.Component {


    //game
    showthisagme(e){
        // fetch('http://localhost:3000/aaa/3',{method : 'get',credentials: 'include'}).then((res)=>{
        //     return res.json()
        // }).then((res)=>{
        //     console.log(res);
        // })
        var game = ReactDOM.findDOMNode(this.refs.game1);
        game.style.display = 'block';
        e.preventDefault();
        e.stopPropagation();
    }
    //game
    hiddenthisgame(e){
        var game = ReactDOM.findDOMNode(this.refs.game1);
        game.style.display = 'none';
        e.preventDefault();
        e.stopPropagation();
    }
    //chat
    showthischat(e){
        var chatNode = ReactDOM.findDOMNode(this.refs.chatRobt);
        chatNode.style.display = 'block';
        e.preventDefault();
        e.stopPropagation();
    }
    //chat
    hiddenthischat(e){
        var chatNode = ReactDOM.findDOMNode(this.refs.chatRobt);
        chatNode.style.display = 'none';
        e.preventDefault();
        e.stopPropagation();
    }
    //game
    replaygame(e){
        var gameReplay = ReactDOM.findDOMNode(this.refs.replay)
            gameReplay.contentWindow.location.reload(true);
        e.preventDefault();
        e.stopPropagation();
    }
    render() {

        return (
            <div className="main_content">
                <div className="text">
                    <article>
                        <div className="article_title"><a></a></div>
                        <div className="article_preview_content"><p>wA,D</p></div>
                    </article>
                    <input onClick={this.showthisagme.bind(this)} type="button" value="" />
                    <div className="game1"ref='game1'>
                        <iframe className="show_game" ref='replay' src="../page/game1/paly.html" scrolling="no" name="ifrmname">
                        </iframe>
                        <input onClick={this.replaygame.bind(this)} type="button" value="" />
                        <input onClick={this.hiddenthisgame.bind(this)} type="button" value="" />
                    </div>
                </div>
                <div className="text">
                    <article>
                        <div className="article_title"><a></a></div>
                        <div className="article_preview_content"><p></p></div>
                    </article>
                    <input onClick={this.showthischat.bind(this)} type="button" value="" />
                    <div className="chat" ref='chatRobt'>
                        <iframe className="show_game" src="../page/chart.html" name="ifrmname">
                        </iframe>
                        <input onClick={this.hiddenthischat.bind(this)} type="button" value="" />
                    </div>
                </div>


            </div>
        );
    }
}

export default DemoComponent;


