import React from 'react'
import { Link ,Redirect} from 'react-router-dom'
import Pubsub from 'pubsub-js'
var Artlength;
class BgFrameConponent extends React.Component{

    constructor(props){
        super(props);
        this.state={
            articlesItem: '11',
            idd:'',
            redirect : false,
            count : '1'
        }
    }
    reflashLoad(e){
        location.reload();
        e.preventDefault();
        e.stopPropagation();
    }
componentWillMount(){
    let articlesTitle=[]
    let articlesId=[]
    let obj={
        method:'get',
        idd:'',
        credentials: 'include'
    }
    fetch('http://localhost:3000/aaa/',obj).then((res)=>{
        return res.json();
    }).then((res)=>{
        // console.log(res[2].title);
        for(let item in res){
            articlesTitle.push(res[item].title);
            articlesId.push(res[item].id);
        }
        //
        Artlength=res.length;
        var a = res.length;
        
        Pubsub.publish('ArticlesLengthTo',a);
        this.props.readData(Artlength);
        this.setState({
            idd : articlesId,
            articlesItem: articlesTitle
        });
    })
    //count
    fetch('http://localhost:3000/fasong',{method:'get', credentials: 'include'}).then((res)=>{
        return res.text()
    }).then((res)=>{
        this.setState({
            count : res
        });
    })
}
//
    isManage(e){
        var url = 'http://localhost:3000/mange';
        var ojbk = {
            method : 'get',credentials: 'include',
            headers : {
                'Access-Control-Allow-Origin':'*'
            },
            credentials: 'include'
        }
        fetch(url,ojbk).then((res)=>{
            return res;
        }).then((res)=>{
            if(res.status==200){
                // this.setState({
                //     redirect : true
                // });
            }else{
                //
            }
        });
        e.preventDefault();
        e.stopPropagation();
    }
    render(){
        if(this.state.redirect){
        return <Redirect push to="/manage" />
        }
        return (
        <div>
        <header className="header">
            <h3></h3>
        </header>
        <div className="navigation">
            {/* lili*/}
            <ul>
                <li><Link to='/' ></Link>
                </li><li><Link to='/login'></Link>
            </li><li><Link to="/leaveMessage" ></Link >
            </li><li><Link to="/picture" ></Link >
            </li><li><Link to="/demo" ></Link >
            </li><li><Link to="/checkArticle/$3"></Link>
            </li><li><Link to="/login"></Link >
            </li>
            </ul>
        </div>
        <div className="sidebar">
            <div className="buddha">
                <div className="buddha_pic">

                </div>
                <span><strong>96sir</strong></span>
            </div>
            <div className="author">
                <ul>
                    <li><label></label></li>
                    <li><label></label></li>
                    <li></li>
                    <p></p>
                </ul>
            </div>
            <div className="recommend">
                <label></label>
                <ul className="aside_ul">
                    <li className="aside_title" ><Link to={'/checkArticle/$'+this.state.idd[0]}>{this.state.articlesItem[0]}</Link></li>
                    <li className="aside_title" ><Link to={'/checkArticle/$'+this.state.idd[1]} >{this.state.articlesItem[1]}</Link></li>
                    <li className="aside_title" ><Link to={'/checkArticle/$'+this.state.idd[2]} >{this.state.articlesItem[2]}</Link></li>
                    <li className="aside_title" ><Link to={'/checkArticle/$'+this.state.idd[3]} >{this.state.articlesItem[3]}</Link></li>
                    <li className="aside_title" ><Link to={'/checkArticle/$'+this.state.idd[4]} >{this.state.articlesItem[4]}</Link></li>
                </ul>
            </div>
            <div className="visited">
                <div>
                    <a>:<span id="counts">{this.state.count}</span></a>
                </div>
            </div>
        </div>
        
    </div>
    );
    }

}


export default BgFrameConponent;



